@import'../../assets/style/styles.scss';

.wrapper {
  margin-bottom: 10px;

  label {
    display: flex;
    flex-direction: column;

    .labelText {
      margin-bottom: 5px;
      cursor: pointer;
      user-select: none;
    }

    input {
      padding: 5px 10px;
      border-radius: 5px;
      display: block;
      width: 100%;
      box-sizing: border-box;
      border: 2px solid $border;
    }
  }

}